<template>
  <div class="container">
    <div class="data">
      <div class="table">
        <biz-sector-list title="选择业务板块" :sectors="sectors" @sectorClick="sectorClick"></biz-sector-list>
        <el-table style="width: 100%" :data="dataSource"  stripe border :header-cell-style="tableHeader">
          <el-table-column
            v-for="column in columns"
            header-align="center"
            :key="column.prop"
            :prop="column.prop"
            :min-width="column.width"
            :label="column.label"
            :align="column.align">
            <el-table-column
              v-for="cc in column.children"
              header-align="center"
              :key="cc.prop"
              :prop="cc.prop"
              :min-width="cc.width"
              :label="cc.label"
              :align="cc.align">
            </el-table-column>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {lifeTable} from '@/api/individual'
const BizSectorList = () => import('./declare/BizSectorList')
export default {
  name: 'TabTable',
  components: {BizSectorList},
  data () {
    return {
      totalData: [], // 返回的总数据
      dataSource: [], // 主表格数据
      currentSector: 0, // 当前选中的板块数组索引
      columns: [
        {
          prop: 'bizSmallType',
          label: '业务小类',
          children: [
            {prop: 'lastYearComInsAmount', label: '商业险上年出险金额', width: 460, align: 'center'},
            {prop: 'lastYearComInsCount', label: '商业险上年出险次数', width: 220, align: 'center'},
            {prop: 'carAge', label: '车龄', width: 120, align: 'center'}
          ]
        },
        {
          prop: 'intelligence',
          label: '智能推荐结果',
          children: [
            {prop: 'comAuto', label: '商业自主系数', width: 120, align: 'center'},
            {prop: 'marketRate', label: '车险市场费用率', width: 130, align: 'center'}
          ]
        },
        {
          prop: 'paramConfig',
          label: '参数配置',
          children: [
            {prop: 'isInverseDiscount', label: '是否反算折扣', width: 120, align: 'center'},
            {prop: 'isInverseCost', label: '是否反算费用', width: 120, align: 'center'},
            {prop: 'targetCost', label: '配置目标成本(%)', width: 140, align: 'center'},
            {prop: 'nonMarketRate', label: '配置非市场费用率(%)', width: 170, align: 'center'},
            {prop: 'comAutoUp', label: '商业险自主系数上限', width: 100, align: 'center'},
            {prop: 'comAutoDown', label: '商业险自主系数下限', width: 100, align: 'center'},
            {prop: 'comMarketRateUp', label: '商业险市场费用率上限(%)', width: 120, align: 'center'},
            {prop: 'comMarketRateDown', label: '商业险市场费用率下限(%)', width: 120, align: 'center'},
            {prop: 'forceMarketRate', label: '交强险市场费用率配置值', width: 120, align: 'center'},
            {prop: 'expComPercent', label: '预期商业险保费占比', width: 110, align: 'center'},
            {prop: 'expForcePercent', label: '预期交强险保费占比', width: 110, align: 'center'},
            {prop: 'expPremiumScale', label: '预期该类业务保费规模(万元)', width: 130, align: 'center'},
            {prop: 'expPremiumPercent', label: '预期该类业务保费占比', width: 110, align: 'center'}
          ]
        },
        {
          prop: 'actualPremiumPercent',
          label: '实际该类业务保费占比',
          children: [
            {prop: 'comAutoAvg', label: '商业险自主系数平均值', width: 110, align: 'center'},
            {prop: 'comMarketRateAvg', label: '商业险市场费用率平均值', width: 120, align: 'center'},
            {prop: 'comMarketRateUpPercent', label: '商业险市场费用率上限占比', width: 120, align: 'center'},
            {prop: 'comMarketRateDownPercent', label: '商业险市场费用率下限占比', width: 120, align: 'center'},
            {prop: 'comFullRate', label: '商业险满期赔付率(含IBNR)', width: 120, align: 'center'},
            {prop: 'comExpCostRate', label: '商业险预期经营成本率(%)', width: 120, align: 'center'},
            {prop: 'forceFinalRate', label: '交强险终极赔付率(%)', width: 120, align: 'center'},
            {prop: 'forceFullRate', label: '交强险满期赔付率(%)(含IBNR)', width: 140, align: 'center'},
            {prop: 'forceExpCostRate', label: '交强险预期经营成本率(%)', width: 120, align: 'center'},
            {prop: 'actualComPercent', label: '实际商业险保费占比', width: 110, align: 'center'},
            {prop: 'actualForcePercent', label: '实际交强险保费占比', width: 110, align: 'center'},
            {prop: 'actualPremiumPercent', label: '实际该类业务保费占比', width: 110, align: 'center'}
          ]
        },
        {
          prop: 'futureCarInsuranceRunForecast',
          label: '车险未来经营情况预测',
          children: [
            {prop: 'totalFinalRate', label: '车险合计终极赔付率(%)', width: 120, align: 'center'},
            {prop: 'totalFullRate', label: '车险合计满期赔付率(%)(含IBNR)', width: 140, align: 'center'},
            {prop: 'totalMarketRate', label: '车险合计市场费用率(%)', width: 120, align: 'center'},
            {prop: 'totalExpCostRate', label: '车险合计预期经营成本率(%)', width: 120, align: 'center'}
          ]
        }
      ]
    }
  },
  computed: mapState({
    sectors: state => state.individual.sectors,
    tableHeader: state => state.app.tableHeader
  }),
  created () {
    lifeTable(this.param).then(response => {
      this.totalData = response.data
      const sectorData = response.data.map(item => {
        return {id: item.id, name: item.name}
      })
      this.$store.commit('SET_SECTORS', sectorData)
      this.dataSource = response.data[this.currentSector].detailData
    })
  },
  methods: {
    sectorClick (id) {
      this.currentSector = this.sectors.findIndex(item => item.id === id)
      this.dataSource = this.totalData[this.currentSector].detailData
    }
  }
}
</script>

<style scoped>

</style>



